.TodoSearch{
     background: #F9fBFC;
     border-radius: 15px;
     border: 2px solid #202329;
     margin: 0 24px;
     height: 45px;
     width: calc(100% - 45px);
     font-size: 24px;
     text-align: center;
     font-family: 'Montserrat';
     font-weight: 400;
     color:  #1E1E1F;
     box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);  
}
.TodoSearch::placeholder{
    color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: #61DAFA;
}